import React from "react";
import style from "./BannerCarousel.module.scss";
import clsx from "clsx";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";

import Button from "@components/Button";
import StarRating from "@components/StarRating";

export default function BannerCarousel({ className, offerProducts, toggleEnquiryModal }) {
  return (
    offerProducts && (
      <Swiper
        id="offer_products_carousel"
        className={clsx(style.wrapper, className)}
        slidesPerView={1}
        allowTouchMove={true}
        speed={1000}
        modules={[Navigation]}
        navigation
        spaceBetween={0}
      >
        {offerProducts.map(({ product, off_percent, offer_price }) => (
          <SwiperSlide key={product.id}>
            {() => (
              <article className={style.slide}>
                <div className={style.productImage}>
                  <img src={product.photo} alt="" />
                </div>
                <div className={style.productInfo}>
                  <div className={style.ribbon}>
                    <div className={style.text}>With {Math.round(off_percent)}% off</div>
                    <div className={style.ribbonTop}></div>
                    <div className={style.ribbonBottom}></div>
                  </div>
                  <div className={style.name}>{product.name}</div>
                  <div className={style.category}>{product.category}</div>
                  <div className={style.rating}>
                    <StarRating count={product.rating} />
                  </div>
                  <div className={style.price}>&#8377; {offer_price}</div>
                  <div className={style.buttons}>
                    <Button onClick={() => toggleEnquiryModal(product)}>Enquire Now</Button>
                  </div>
                </div>
              </article>
            )}
          </SwiperSlide>
        ))}
      </Swiper>
    )
  );
}
