import React, { useState } from "react";
import style from "./Enquiry.module.scss";

import { useForm } from "react-hook-form";
import { ClipLoader } from "react-spinners";

import EnquiryModal from "@components/EnquiryModal";
import { postProductEnquiry } from "@apis/products";

export default function Enquiry({ activeProduct, onClose }) {
  const [apiError, setApiError] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  const product = activeProduct;

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({ mode: "onBlur" });

  function onSubmit(data) {
    setApiError(false);
    setIsLoading(true);
    data = { ...data, product: product.id };
    postProductEnquiry(data)
      .then((resp) => {
        onClose();
      })
      .catch((err) => {
        console.log(err);
        setApiError(true);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }

  return (
    <EnquiryModal onClose={onClose}>
      <form className={style.wrapper} onSubmit={handleSubmit(onSubmit)}>
        <input placeholder="Enter name of your child" {...register("name", { required: "* Required" })} />
        {errors.name && <p>{errors.name.message}</p>}

        <input
          placeholder="Email Address"
          {...register("email", {
            required: "* Required",
            pattern: {
              value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
              message: "invalid email address",
            },
          })}
        />
        {errors.email && <p>{errors.email.message}</p>}

        <input
          type="number"
          placeholder="Mobile Number"
          {...register("phone", {
            required: "* Required",
            maxLength: 11,
            minLength: 8,
          })}
        />
        {errors.phone && <p>Please enter valid phone number.</p>}

        <input type="text" readOnly value={product.name} />

        <textarea {...register("message", { required: "* Required" })} placeholder="Type your message" />
        {errors.message && <p>{errors.message.message}</p>}

        {apiError && <p className={style.apiError}>An error occured. Please try again</p>}

        <button disabled={isLoading} type="submit">
          {isLoading ? <ClipLoader size={20} color="#9f0b91" /> : "Submit"}
        </button>
      </form>
    </EnquiryModal>
  );
}
