import React from "react";
import style from "./CategorySelector.module.scss";

import { BiFilter } from "react-icons/bi";
import Select from "react-select";

export default function CategorySelector({ categories, onChange }) {
  const _WIDTH = "150px";
  const _FONT_SIZE = "14px";

  const colourStyles = {
    container: (styles, { isFocused }) => ({
      ...styles,
      borderColor: "transparent !important",
      width: _WIDTH,
    }),
    control: (styles, { isFocused }) => ({
      ...styles,
      boxShadow: "unset !important",
      borderColor: "transparent !important",
      fontSize: _FONT_SIZE,
      backgroundColor: "white",
      borderRadius: "5px",
      width: _WIDTH,
      minHeight: "0px",
      height: "32px",
      cursor: "pointer",
    }),
    option: (styles, { data, isDisabled, isFocused, isSelected }) => {
      return {
        ...styles,

        fontSize: _FONT_SIZE,
        textAlign: "right",
        backgroundColor: isSelected ? "#a34791" : "white",
        cursor: "pointer",
        "&:hover": {
          backgroundColor: "rgba(1,1,1,0.1)",
        },
      };
    },
    input: (styles) => ({ ...styles, color: "transparent" }),
  };
  const options = categories.map((item) => ({ label: item, value: item }));
  options.unshift({
    label: (
      <div style={{ display: "flex", alignItems: "center" }}>
        <BiFilter size={30} style={{ marginRight: "0px" }} />
        <span style={{ textAlign: "right", width: "100%" }}>All Categories</span>
      </div>
    ),
    value: "all",
  });

  return (
    <div className={style.wrapper}>
      {true && (
        <div className={style.selector}>
          <Select
            defaultValue={options[0]}
            options={options}
            styles={colourStyles}
            onChange={onChange}
            components={{
              IndicatorSeparator: () => null,
              DropdownIndicator: () => null,
            }}
          />
        </div>
      )}
    </div>
  );
}
