import React, { useEffect, useState } from "react";
import style from "./index.module.scss";

import clsx from "clsx";
import { useWindowResize } from "beautiful-react-hooks";
import { ImStarFull } from "react-icons/im";

import WithPadding from "@components/WithPadding";
import SectionHeader from "@components/SectionHeader";
import Button from "@components/Button";
import InlineLoader from "@components/InlineLoader";
import CongratsModal from "@components/CongratsModal";

import ProductModal from "./ProductModal";
import BannerCarousel from "./BannerCarousel";
import CategorySelector from "./CategorySelector";
import Enquiry from "./Enquiry";

import useProducts from "./useProducts";
import { splitArrChunks } from "@utils/helpers";

const _Uid = ["ABC", "DEF", "TYP"];

export default function Products() {
  const { state, changeActiveCat, toggleProdModal, toggleEnquiryModal } = useProducts();
  const { prodsByCat, activeCat, isLoading } = state;
  const filteredProducts = prodsByCat[activeCat];

  const [prodChunks, setProdChunks] = useState([]);
  const [rowSize, setRowSize] = useState(4);

  function updateRowSize() {
    if (window.innerWidth < 1350 && window.innerWidth > 1050) {
      setRowSize(3);
    } else if (window.innerWidth < 1050 && window.innerWidth > 650) {
      setRowSize(2);
    } else if (window.innerWidth < 650) {
      setRowSize(1);
    } else {
      setRowSize(4);
    }
  }

  useEffect(() => {
    setProdChunks(splitArrChunks(filteredProducts, rowSize));
  }, [filteredProducts, rowSize]);

  useWindowResize(updateRowSize);
  useEffect(() => {
    updateRowSize();
  }, []);

  return isLoading ? (
    <InlineLoader height="90vh" />
  ) : (
    <main className={clsx(style.wrapper, state.isOpenModal && style.openModal)}>
      <section className={style.banner}>
        <WithPadding>
          <div className={style.underlay}></div>
          <BannerCarousel
            className={style.carouselWrapper}
            toggleEnquiryModal={toggleEnquiryModal}
            offerProducts={state.offerProds}
          />
        </WithPadding>
      </section>
      <WithPadding className={style.productsSection}>
        <header>
          <SectionHeader>All Products</SectionHeader>
          <CategorySelector categories={state.allCats} onChange={changeActiveCat} />
        </header>

        <section className={style.products}>
          {prodChunks.map((prodRow, indexRow) => (
            <div className={style.prodRow} key={_Uid[indexRow]}>
              {prodRow.map((product) => (
                <article key={product.id}>
                  <div className={style.productImage}>
                    <img src={product.photo} alt="" />
                    <div className={style.rating}>
                      {product.rating} <ImStarFull size={12} />
                    </div>
                  </div>
                  <div className={style.productInfo}>
                    <div className={style.name}>{product.name}</div>
                    <div className={style.category}>{product.category}</div>
                    <div className={style.price}>&#8377; {product.price}</div>
                    <div className={style.buttons}>
                      <Button onClick={() => toggleEnquiryModal(product)}>Enquire Now</Button>
                      <Button type="light" onClick={() => toggleProdModal(product)}>
                        View More
                      </Button>
                    </div>
                  </div>
                </article>
              ))}
            </div>
          ))}
        </section>
      </WithPadding>

      {state.isOpenModal && (
        <ProductModal
          toggleEnquiryModal={toggleEnquiryModal}
          isOpen={state.isOpenModal}
          onClose={toggleProdModal}
          productId={state.activeModalId}
          activeProduct={state.activeProduct}
        />
      )}
      {state.isOpenCongratsModal && <CongratsModal />}
      {state.isOpenEnquiryModal && (
        <Enquiry activeProduct={state.activeProduct} product={state.activeEnquiryProd} onClose={toggleEnquiryModal} />
      )}
    </main>
  );
}
