import React, { useEffect, useState } from "react";
import style from "./ProductModal.module.scss";

import Button from "@components/Button";
import Modal from "@components/Modal";

import { getProduct } from "@apis/products";

export default function ProductModal({ activeProduct, productId, isOpen, onClose, toggleEnquiryModal }) {
  const [product, setProduct] = useState({});

  function populateProduct() {
    getProduct(productId)
      .then((resp) => {
        setProduct(resp.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  useEffect(() => {
    populateProduct();
  }, [productId]);

  return (
    <Modal className={style.wrapper} isOpen={isOpen} onClose={onClose}>
      <article key={product.id}>
        <div className={style.productImage}>
          <img src={product.photo} alt="" />
        </div>
        <div className={style.productInfo}>
          <div className={style.name}>
            {product.name} <span>{product.category}</span>
          </div>
          <div className={style.price}>&#8377; {product.price}</div>
          <div className={style.description}>{product.description}</div>

          <Button onClick={() => toggleEnquiryModal(activeProduct)}>Enquire Now</Button>
        </div>
      </article>
    </Modal>
  );
}
