import React, { useState, useCallback, useEffect } from "react";
import style from "./index.module.scss";

import { ImStarHalf, ImStarFull } from "react-icons/im";

export default function StarRating({ count }) {
  const [state, setState] = useState([]);

  const setArr = useCallback(() => {
    let arr = [];
    if (count % 1 === 0) {
      for (let i = 0; i < count; i++) {
        arr.push({
          count: i,
          isHalf: false,
        });
      }
    } else {
      for (let i = 0; i < Math.floor(count); i++) {
        arr.push({
          count: i,
          isHalf: false,
        });
      }
      arr.push({
        count: Math.floor(count) + 1,
        isHalf: true,
      });
    }

    setState(arr);
  }, [count]);

  useEffect(() => {
    setArr();
  }, []);

  return (
    <div className={style.wrapper}>
      {state.map((star) =>
        star.isHalf ? <ImStarHalf size={11} key={star.count} /> : <ImStarFull size={11} key={star.count} />
      )}
    </div>
  );
}
