import { useEffect, useReducer } from "react";
import { getAllProducts, getOfferProducts } from "@apis/products";

/* Terminology
   prod => products
   cat => category 
   act => active
*/

const initialState = {
  isLoading: true,
  prodsByCat: {
    all: [],
  },
  activeCat: "all",
  allCats: [],
  offerProducts: [],
  isOpenModal: false,
  isOpenEnquiryModal: false,
  isOpenCongratsModal: false,
  activeModalId: "",
  activeEnquiryProd: {},
  activeProduct: {},
};

function reducer(state, action) {
  const { type, payload } = action;
  switch (type) {
    case "SET_IS_LOADING":
      return { ...state, isLoading: payload.isLoading };
    case "SET_PRODS_BY_CAT":
      return { ...state, prodsByCat: payload.prodsByCat };
    case "SET_OFFER_PRODS":
      return { ...state, offerProds: payload.offerProds };
    case "SET_CATS":
      return { ...state, allCats: payload.allCats };
    case "SET_ACTIVE_CAT":
      return { ...state, activeCat: payload.activeCat };
    case "SET_ACTIVE_MODAL_ID":
      return { ...state, activeModalId: payload.activeModalId };
    case "SET_ACT_PROD":
      return { ...state, activeProduct: payload.activeProduct };
    case "SET_IS_OPEN_MODAL":
      return { ...state, isOpenModal: payload.isOpenModal };
    case "SET_IS_OPEN_ENQUIRY_MODAL":
      return { ...state, isOpenEnquiryModal: payload.isOpenEnquiryModal };
    case "SET_IS_OPEN_CONGRATS_MODAL":
      return { ...state, isOpenCongratsModal: payload.isOpenCongratsModal };
    case "SET_ACT_ENQUIRY_PROD":
      return { ...state, activeEnquiryProd: payload.activeEnquiryProd };
    default:
      return state;
  }
}
export default function useCourses() {
  const [state, dispatch] = useReducer(reducer, initialState);

  function changeActiveCat(cat) {
    dispatch({ type: "SET_ACTIVE_CAT", payload: { activeCat: cat.value } });
  }

  function toggleProdModal(prod) {
    dispatch({ type: "SET_IS_OPEN_MODAL", payload: { isOpenModal: !state.isOpenModal } });

    if (prod) dispatch({ type: "SET_ACTIVE_MODAL_ID", payload: { activeModalId: prod.id } });
    if (prod) dispatch({ type: "SET_ACT_PROD", payload: { activeProduct: prod } });
  }

  function toggleEnquiryModal(prod) {
    dispatch({ type: "SET_IS_OPEN_ENQUIRY_MODAL", payload: { isOpenEnquiryModal: !state.isOpenEnquiryModal } });
    dispatch({ type: "SET_IS_OPEN_MODAL", payload: { isOpenModal: false } });

    if (prod) dispatch({ type: "SET_ACT_ENQUIRY_PROD", payload: { activeEnquiryProd: prod } });
    if (prod) dispatch({ type: "SET_ACT_PROD", payload: { activeProduct: prod } });

    if (!prod) toggleCongratsModal();
  }

  function toggleCongratsModal() {
    dispatch({ type: "SET_IS_OPEN_CONGRATS_MODAL", payload: { isOpenCongratsModal: true } });

    window.setTimeout(() => {
      dispatch({ type: "SET_IS_OPEN_CONGRATS_MODAL", payload: { isOpenCongratsModal: false } });
    }, 4000);
  }

  function populateOfferProducts() {
    getOfferProducts().then((resp) => {
      dispatch({ type: "SET_OFFER_PRODS", payload: { offerProds: resp.data } });
    });
  }

  function populateProducts() {
    dispatch({ type: "SET_IS_LOADING", payload: { isLoading: true } });
    getAllProducts()
      .then((resp) => {
        const allProds = resp.data;

        let obj = {
          all: allProds,
        };
        let catsSet = new Set();

        allProds.forEach((prod) => {
          obj[prod.category] = [];
          catsSet.add(prod.category);
        });
        allProds.forEach((course) => {
          obj[course.category].push(course);
        });

        dispatch({ type: "SET_CATS", payload: { allCats: Array.from(catsSet) } });
        dispatch({ type: "SET_PRODS_BY_CAT", payload: { prodsByCat: obj } });
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        dispatch({ type: "SET_IS_LOADING", payload: { isLoading: false } });
      });
  }

  useEffect(() => {
    populateProducts();
    populateOfferProducts();
  }, []);

  return { state, changeActiveCat, toggleProdModal, toggleEnquiryModal };
}
