import { getRequest, postRequest } from "./setup";

export function getAllProducts() {
  return getRequest(`/products`);
}

export function getOfferProducts() {
  return getRequest(`/products/offers`);
}

export function getProduct(id) {
  return getRequest(`/products/${id}`);
}

export function postProductEnquiry(data) {
  return postRequest(`/general/product-enquiry/`, data);
}
